import React,{useContext,useState,useEffect} from 'react';
import { MyContext } from '../../contexts/MyContext';
import {Link } from "react-router-dom"; 
import Loader from 'react-loader-spinner';
import { FiEdit2 } from "react-icons/fi";
import Moment from 'react-moment';
import Navbarmenu from "../menu/Navbarmenu";
import Footer from "../Footer";
import Mylist from "./Mylist";
import Mylisttwo from "./Mylisttwo";

import '../../styles/_property.scss';

import { WEB_URL_UPLOAD } from '../../contexts/Apilinks';
let BaseURLuploads = WEB_URL_UPLOAD;


const AccountAll = () => {
  const {rootState,userlistall} = useContext(MyContext);
  const {isAuth,theUserid} = rootState;
  useEffect(() => {
         userlist(theUserid);
         window.scrollTo(0, 0);
  }, []);    
  
  const [isuserlist, setuserlist] = useState([]);
  const userlist = async(ids) =>{
         try {
                const userlistresults = await userlistall(ids);
                       if(userlistresults.success === 1){
                              console.log('userlistresults',userlistresults.userlistviews);
                              setuserlist(userlistresults.userlistviews);
                }
         } catch (error) { throw error;}
       }
       const [isBuyRent, setBuyRent] = useState(true);
       const [isPg, setPg] = useState(false);
       const BuyRent = () =>{
              setBuyRent(true);
              setPg(false);
       }
        const Pg = () =>{
              setBuyRent(false);
              setPg(true);
       }
       
      
       
  return (
    <>
    <Navbarmenu />
    <div className="authors__sidebar">
    <div className="container">
    <div className="row">
    {isuserlist.map((item, index) => (  
       <div  className="col-md-12" key={item.register_id} >
             
              <div className="profile__image">
              <div className="col-md-12">
              {item.profileimage !== '' ? <> 
                            <img src={BaseURLuploads+item.profileimage} className="profsssimages" alt="profileimage" />  </> 
                     : <>
                            <div className="profsssimages2">  <span className="lognosss__spans2sss"> {item.name.charAt(0)} </span> </div>
                     </>} 
                     <h2 className="profile__names">  {item.name} </h2>
                     <div className="profile__text" > <FiEdit2 /> Edit Profile</div>
              </div>
              </div>
</div>
))}

<ul>
<li onClick={BuyRent}> Buy/Rent</li>
<li onClick={Pg}> PG</li>
</ul>

{isBuyRent === true ? <> <Mylist storageids={theUserid} />  </>: null}
{isPg === true ? <> <Mylisttwo storageids={theUserid} />   </>: null}












</div>
</div>
</div>
      
    <Footer />         
		</>
  )
}

export default AccountAll
