import React,{useContext,useState,useEffect} from 'react';
import { MyContext } from '../../contexts/MyContext';
import {Link } from "react-router-dom"; 
import Loader from 'react-loader-spinner';
import { FiChevronsRight,FiEdit2 } from "react-icons/fi";
import Moment from 'react-moment';
import Navbarmenu from "../menu/Navbarmenu";
import Footer from "../Footer";
import Propertyviewimage from "../property/Propertyviewimage";
import ReactReadMoreReadLess from "react-read-more-read-less";

import { WEB_URL_UPLOAD } from '../../contexts/Apilinks';
let BaseURLuploads = WEB_URL_UPLOAD;

const Mylist = (props) => {
  const {buysalladmin} = useContext(MyContext);
  useEffect(() => {
         buysall(page,props.storageids);
  }, []);    
  const [page, setPage] = useState(1);
  const [isbuys, setbuys] = useState([]);
  const [loademoreloding, setloademoreloding] = useState(false);
  const [ispostloademore, setpostloademore] = useState(false);
  const buysall = async(page,pageId) =>{
    try {
      setloademoreloding(true);
      const buysss = await buysalladmin(page,pageId);
      if(buysss.success === 1){
        console.log('buysss',buysss.buysssviews);
        if(buysss.buysssviews.length > 0){
          setbuys(prevBooks => {
            return [...prevBooks, ...buysss.buysssviews]
          })
               setpostloademore(false);
               setloademoreloding(false);
          }else{
                setpostloademore(true);
                setloademoreloding(false);
          }
      }
    } catch (error) { throw error;}
  }
const [count, setCount] = useState(1);
const handleIncrement = (count) => {
    setCount(prevCount => prevCount + 1);
    console.log('count',count+1);
    buysall(count+1);
};

  return (
    <div className="Rentsss">
      
{isbuys.map((item, index) => (  
  <div className="col-md-12 buysss" key={item.property_id} >
  {item.propertyDetails === 'Sale' ? <>  
    <div  className="col-md-3"  >
       <Propertyviewimage ids={item.property_id} />
     <p className="align__center"> Posted: <Moment format="DD MMM YYYY" withTitle>
        {item.date}
       </Moment> </p>
       
    </div>
    <div  className="col-md-9"  >     

  <h2> <b> {item.Bedrooms !== '' ? <> {item.Bedrooms} BHK </> : null } {item.propertytype} </b> for Sale in {item.Locality}  {item.City} </h2>
<ul className="listbuys">
      {item.Carpet !== '' ? <li>Carpet Area   <br/> <span> {item.Carpet} {item.Carpetsq}</span></li>
      : <li>Super Area  <br/> <span> {item.Super} {item.Supersq}</span></li>
      }
      
    
      {item.TransactionType !== '' ? 
        <li>Status <br/> <span> {item.TransactionType} </span> </li>
      : null}
      {item.Age !== '' ? 
        <li> Age of Construction <br/> <span> {item.Age === 'New Construction' ? 'New Property' : <> {item.Age} </> } </span> </li>
      : null}
        
      {item.Bedrooms !== '' ? 
        <li>Bedrooms  <br/> <span> {item.Bedrooms}  </span> </li>
      : null}
      {item.Balconies !== '' ? 
        <li>Balconies  <br/> <span> {item.Balconies}  </span> </li>
      : null}
      {item.Bathrooms !== '' ? 
        <li>Bathrooms  <br/> <span> {item.Bathrooms}  </span> </li>
      : null}
      {item.water !== '' ? 
        <li>Water Availability  <br/> <span> {item.water}  </span> </li>
      : null}
      {item.electricity !== '' ? 
        <li>Status of Electricity  <br/> <span> {item.electricity}  </span> </li>
      : null}
       {item.parking_covered !== '' || item.parking_open !== '' && item.parking_none === ''  ? 
    <li>Car parking <br /> <span>{item.parking_covered_no} {item.parking_covered} | {item.parking_open_no} {item.parking_open}</span></li>: null}

    {item.parking_covered === '' && item.parking_open === '' && item.parking_none !== ''  ? 
    <li>Car parking <br /> <span>{item.parking_none} </span></li>: null}
    {item.FurnishedStatus !== '' ? <li>Furnished status <br /> <span> {item.FurnishedStatus} </span></li>: null}
    {item.FurnishedStatus !== '' ? <li>Furnished status <br /> <span> {item.FurnishedStatus} </span></li>: null}
        
      </ul>

       <p className="pricesss"> ₹{item.ExpectedPrice} </p>
      
       {item.description !== '' ? <p>
       <ReactReadMoreReadLess
        charLimit={200}
        readMoreText={"read more"}
        readLessText={"read less"}>
         {item.description}
        </ReactReadMoreReadLess>
      </p>: null}
{/* </> : null } */}


<div className="clearfix"></div>

<Link to ={`/PropertyView/${item.property_id}`}> View </Link>
<Link to ={`/PropertyEdit/${item.property_id}`} className="btn2"> <FiEdit2 /> Edit </Link>
<Link to ={`/Propertyimgorder/${item.property_id}`} className="btn2"> <FiEdit2 /> Edit Image </Link>

    
  </div> 
  </>: null }

{/* --------------------------------------------------------------------- */}


  {item.propertyDetails === 'Rent/Lease' ? <>  
  <div  className="col-md-3"  >
       <Propertyviewimage ids={item.property_id} />
       <p className="align__center"> Posted: <Moment format="DD MMM YYYY" withTitle>
        {item.date}
       </Moment> </p>
    </div>
     

<div  className="col-md-9"  >     

  <h2> <b> {item.Bedrooms !== '' ? <> {item.Bedrooms} BHK </> : null } {item.propertytype} </b> for Sale in {item.Locality}  {item.City} </h2>
<ul className="listbuys">
      {item.Carpet !== '' ? <li>Carpet Area   <br/> <span> {item.Carpet} {item.Carpetsq}</span></li>
      : <li>Super Area  <br/> <span> {item.Super} {item.Supersq}</span></li>
      }
      
      <li>Status <br/> <span>{item.availablefrom === 'Immediately'? <> Immediately Available </> : <> Available From : <Moment format="DD MMM YYYY" withTitle>
        {item.datevalues}
       </Moment>  </> } </span></li>
    
      {item.Age !== '' ? 
        <li> Age of Construction <br/> <span> {item.Age === 'New Construction' ? 'New Property' : <> {item.Age} </> } </span> </li>
      : null}
        
      {item.Bedrooms !== '' ? 
        <li>Bedrooms  <br/> <span> {item.Bedrooms}  </span> </li>
      : null}
      {item.Balconies !== '' ? 
        <li>Balconies  <br/> <span> {item.Balconies}  </span> </li>
      : null}
      {item.Bathrooms !== '' ? 
        <li>Bathrooms  <br/> <span> {item.Bathrooms}  </span> </li>
      : null}
      {item.water !== '' ? 
        <li>Water Availability  <br/> <span> {item.water}  </span> </li>
      : null}
      {item.electricity !== '' ? 
        <li>Status of Electricity  <br/> <span> {item.electricity}  </span> </li>
      : null}
        {item.parking_covered !== '' || item.parking_open !== '' && item.parking_none === ''  ? 
    <li>Car parking <br /> <span>{item.parking_covered_no} {item.parking_covered} | {item.parking_open_no} {item.parking_open}</span></li>: null}

    {item.parking_covered === '' && item.parking_open === '' && item.parking_none !== ''  ? 
    <li>Car parking <br /> <span>{item.parking_none} </span></li>: null}
    {item.FurnishedStatus !== '' ? <li>Furnished status <br /> <span> {item.FurnishedStatus} </span></li>: null}
    {item.FurnishedStatus !== '' ? <li>Furnished status <br /> <span> {item.FurnishedStatus} </span></li>: null}
        
      </ul>

       <p className="pricesss"> ₹{item.monthlyrent} </p>
      
       {item.description !== '' ? <p>
       <ReactReadMoreReadLess
        charLimit={200}
        readMoreText={"read more"}
        readLessText={"read less"}>
         {item.description}
        </ReactReadMoreReadLess>
      </p>: null}
{/* </> : null } */}


<div className="clearfix"></div>

<Link to ={`/PropertyView/${item.property_id}`}> View </Link>
<Link to ={`/PropertyEdit/${item.property_id}`} className="btn2"> <FiEdit2 /> Edit </Link>
<Link to ={`/Propertyimgorder/${item.property_id}`} className="btn2"> <FiEdit2 /> Edit Image </Link>
</div>


  </>: null } 
  </div> 
))}
       
       {ispostloademore === false ? 
       <div className="loading" >
              {loademoreloding  ?
              <>
                     <Loader type="ThreeDots" color="#1da1f3" height={67} width={67} style={{float: "left",marginTop:"37px",display:"block",width:"100%"}}/>
              </>: 
                     <button className="pagination__buttonsss"  onClick={()=>handleIncrement(count)}> Next <FiChevronsRight /> </button>
              }
       </div>
: null }

    </div>
  )
}

export default Mylist
